import { View, Text, SafeAreaView ,Image, StyleSheet,Animated, TouchableOpacity} from 'react-native'
import { ScrollView } from 'react-native-gesture-handler'
import Icon from 'react-native-vector-icons/FontAwesome';
import LinearGradient from 'react-native-linear-gradient';
import React, { useEffect, useRef } from 'react'
import { transaction } from '../../utils/dataTrans';
import TransactionCard from '../../utils/transactionCard';
import { useNavigation } from '@react-navigation/native';
export default function Home() {
  const fadeAnim = useRef(new Animated.Value(0)).current;
  const slideAnim = useRef(new Animated.Value(30)).current;
  const Navigation=useNavigation();
  useEffect(() => {
    Animated.parallel([
      Animated.timing(fadeAnim, {
        toValue: 1,
        duration: 600,
        useNativeDriver: true
      }),
      Animated.timing(slideAnim, {
        toValue: 0,
        duration: 600,
        useNativeDriver: true
      })
    ]).start();
  }, []);
    const getIcon = () => {
    switch(transaction.type){
      case 'dépense':
        return <Icon name="arrow-up" size={20} color="#F43F5E" />;
      case 'revenu':
        return <Icon name="arrow-down" size={20} color="#10B981" />; 
      case 'transfert':
        return <Icon name="exchange" size={20} color="#3B82F6" />; 
      default:
        return null;
    }
  };


  const amountColor = transaction.type === 'dépense' ? '#F43F5E' : transaction.type === 'revenu' ? '#10B981' : '#3B82F6';


  return (
     <SafeAreaView style={{flex:1}}>
        <ScrollView style={{flex:1}} showsVerticalScrollIndicator={false} showsHorizontalScrollIndicator={false}>
          
        
          <View style={{backgroundColor:'#0D1B2A',borderBottomLeftRadius:25,borderBottomRightRadius:25,paddingBottom:16}}>
            <View style={style.top}>
              <View style={{flexDirection:'row',alignItems:'center',gap:8}}>
                <Image source={require('../../assets/logo/icon-512.png')} style={style.avatar}/>
                <Text style={{color:'white',fontSize:22,fontWeight:'bold'}}>Bienvenue, Beriole</Text>
              </View>
              <Icon name="bell" onPress={()=>Navigation.navigate('Notification')} size={28} color="white" />
            </View>

           
            <View style={{justifyContent:'center',alignItems:'center',marginTop:12}}>
              <LinearGradient colors={['#1E90FF', '#00C49A']} style={style.cardWallet}>
                <Text style={style.walletTitle}>Portefeuille</Text>
                <Text style={style.walletSubtitle}>Solde Total</Text>
                <Text style={style.walletBalance}>10 000 000 000 000 FCFA</Text>
              </LinearGradient>
            </View>

           
            <Animated.View style={[style.walletRow,{opacity: fadeAnim, transform:[{translateY: slideAnim}]}]}>
              
              <LinearGradient colors={['#3B82F6', '#60A5FA']} style={style.wallet}>
                <Icon name="credit-card" size={24} color="#fff" />
                <Text style={style.walletName}>Solde Courant</Text>
                <Text style={style.walletAmount} numberOfLines={1} ellipsizeMode="tail">70 000 FCFA</Text>
              </LinearGradient>

              <LinearGradient colors={['#8B5CF6', '#A78BFA']} style={style.wallet}>
                <Icon name="bullseye" size={24} color="#fff" />
                <Text style={style.walletName}>Solde Projets</Text>
                <Text style={style.walletAmount} numberOfLines={1} ellipsizeMode="tail">50 000 FCFA</Text>
              </LinearGradient>

              <LinearGradient colors={['#10B981', '#34D399']} style={style.wallet}>
                <Icon name="money" size={24} color="#fff" />
                <Text style={style.walletName}>Solde Épargne</Text>
                <Text style={style.walletAmount} numberOfLines={1} ellipsizeMode="tail">27 000 FCFA</Text>
              </LinearGradient>

            </Animated.View>
          </View>
          
          <View style={style.actionCard}>
                  <TouchableOpacity style={style.actionItem} onPress={()=>Navigation.navigate('Depense')}>
                    <Icon name="shopping-cart" size={26} color="#F43F5E" onPress={()=>Navigation.navigate('Depense')}/>
                    <Text style={style.actionLabel}>Dépenser</Text>
                  </TouchableOpacity>
                  
                  <TouchableOpacity style={style.actionItem} onPress={()=>Navigation.navigate('Recharge')}>
                    <Icon name="plus-circle" size={26} color="#10B981" />
                    <Text style={style.actionLabel}>Déposer</Text>
                  </TouchableOpacity>
                  
                  <TouchableOpacity style={style.actionItem} onPress={()=>Navigation.navigate('TransfertFonds')}>
                    <Icon name="exchange" size={26} color="#3B82F6" />
                    <Text style={style.actionLabel}>Transférer</Text>
                  </TouchableOpacity>
                  <TouchableOpacity style={style.actionItem} onPress={()=>Navigation.navigate('Transaction')}>
                    <Icon name="history" size={26} color="#F59E0B"  />
                    <Text style={style.actionLabel}>Historique</Text>
                  </TouchableOpacity>
           </View>
            <View>
              <View style={{flexDirection:'row',alignItems:'center',justifyContent:'space-between',marginHorizontal:16,marginTop:12}}>
                <Text style={{color:'black',fontWeight:'bold',fontSize:16}}>
                    transactions Recentes
                </Text>
                <Text style={{color:'green',fontWeight:'bold',fontSize:16}} onPress={()=>Navigation.navigate('Transaction')}>
                   voir tout
                </Text>
              </View>
              <ScrollView>
                {transaction.map((tx, index) => (
               <TransactionCard key={index} transaction={tx} />
                   ))}
              </ScrollView>
            </View>
        </ScrollView>
     </SafeAreaView>
  )
}

const style = StyleSheet.create({
  avatar:{
    height:40,
    width:40,
    borderRadius:100
  },
  top:{
    flexDirection:'row',
    justifyContent:'space-between',
    alignItems:'center',
    margin:16
  },
cardWallet: {
    width: "90%",
    borderRadius: 16,
    padding: 12,      
    alignItems: 'center',
    shadowColor: '#000',
    shadowOpacity: 0.1,
    shadowRadius: 4,
    elevation: 3,
  },
walletTitle: {
    fontSize: 18,    
    color: '#fff',
    fontWeight: 'bold',
  },
walletSubtitle: {
    fontSize: 13,     
    color: '#e0e0e0',
    marginTop: 4,
  },
walletBalance: {
    fontSize: 20,      
    fontWeight: 'bold',
    color: '#fff',
    marginTop: 6,
  },
  walletRow:{
    flexDirection:'row',
    justifyContent:'space-around',
    alignItems:'center',
    marginTop:20,
    paddingHorizontal:12,
  },
  wallet:{
    flex:1,
    borderRadius:16,
    padding:16,
    marginHorizontal:6,
    shadowColor:'#000',
    shadowOpacity:0.1,
    shadowRadius:4,
    elevation:3,
    alignItems:'center',
  },
  walletName:{
    fontSize:16,
    color:'#fff',
    fontWeight:'600',
    marginTop:6,
  },
  walletAmount:{
    fontSize:14,
    color:'#fff',
    marginTop:2,
  },
  actionCard:{
  flexDirection:'row',
  justifyContent:'space-around',
  alignItems:'center',
  backgroundColor:'#fff',
  marginTop:15,
  marginHorizontal:16,
  paddingVertical:16,
  borderRadius:16,
  shadowColor:'#000',
  shadowOpacity:0.08,
  shadowRadius:4,
  elevation:3,
},

actionItem:{
  alignItems:'center',
  justifyContent:'center',
  flex:1
},

actionLabel:{
  fontSize:14,
  color:'#333',
  marginTop:6,
  fontWeight:'500'
}
})
